.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.log-container {
  border: 1px solid #ddd;
  padding: 10px;
  max-height: 400px;
  overflow-y: scroll;
  white-space: pre-wrap;
  background-color: #f9f9f9;
  color: black;
  width: 80%;
  margin-top: 20px;
  text-align: left;
}

select, button {
  margin: 10px;
  padding: 10px;
  font-size: 16px;
}
